#section1 {
    height: calc(890px - 6rem);
    margin: 0;
    .section1-wrapper {
        height: 90%;
        display: grid;
        grid-template-columns: 9fr 5fr;
        grid-template-rows: 100%;
        overflow: hidden;
        .img-wrapper {
            width: 100%;
            display: grid;
            grid-template-rows: repeat(20, 1fr);
            grid-template-columns: repeat(40, 1fr);
            img {
                width: 100%;
                height: 100%;
                border-radius: 0.6rem;
            }
            #camPic1 {
                grid-row: 1/10;
                grid-column: 1/23;
            }
            #camPic2 {
                grid-row: 4/10;
                grid-column: 24/32;
            }
            #camPic3 {
                grid-row: 11/17;
                grid-column: 3/11;
            }
            #camPic4 {
                grid-row: 12/21;
                grid-column: 12/25;
            }
            #camPic5 {
                grid-row: 11/19;
                grid-column: 26/35;
            }
        }

        .title {
            margin: auto 0;
            text-align: center;
        }
    }
}

#section2 {
    height: 890px;
    background: #edfdf7;
    margin: 0;
    .section2-wrapper {
        height: 90%;
        display: grid;
        grid-template-columns: 5fr 9fr;
        grid-template-rows: 100%;
        .img-wrapper {
            width: 100%;
            display: grid;
            grid-template-rows: repeat(20, 1fr);
            grid-template-columns: repeat(40, 1fr);
            img {
                width: 100%;
                height: 100%;
                border-radius: 0.6rem;
            }
            #eventsPic1 {
                grid-row: 1/10;
                grid-column: 6/28;
            }
            #eventsPic2 {
                grid-row: 4/10;
                grid-column: 29/37;
            }
            #eventsPic3 {
                grid-row: 11/17;
                grid-column: 8/16;
            }
            #eventsPic4 {
                grid-row: 12/21;
                grid-column: 17/30;
            }
            #eventsPic5 {
                grid-row: 11/19;
                grid-column: 31/40;
            }
        }

        .title {
            margin: auto 0;
            text-align: center;
        }
    }
}

#section3 {
    height: 500px;
    margin: 0;
    .section3-wrapper {
        display: flex;
        height: 90%;

        img {
            width: 250px;
            height: 300px;
            margin: auto 100px auto 0;
            border-radius: 10px;
        }
        .title {
            flex: 1;
            margin: auto 0;
            text-align: center;
        }
    }
}

#section4 {
    height: 1300px;
    background: #f5f5ff;
    margin: 0;
    .section4-wrapper {
        display: flex;
        height: 500px;
        .title {
            margin: auto 0;
            text-align: center;
            flex: 1;
        }
        img {
            border-radius: 10px;
            width: 380px;
            height: 250px;
            margin: auto 0 auto 100px;
        }
    }
}

.title {
    font-family: Comfortaa;
    font-style: normal;
    font-size: 3rem;
    font-weight: bold;
    line-height: 3rem;
    color: #000000;
}

@media screen and (max-width: 1100px) {
    #section1 {
        height: 100%;
        .section1-wrapper {
            margin: 0 auto 60px auto;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr 5fr;
            .title {
                grid-row: 1/2;
                grid-column: 1/2;
                display: flex;
                justify-content: center;
            }
            .img-wrapper {
                #camPic1 {
                    grid-column: 4/26;
                }
                #camPic2 {
                    grid-column: 27/35;
                }
                #camPic3 {
                    grid-column: 6/14;
                }
                #camPic4 {
                    grid-column: 15/28;
                }
                #camPic5 {
                    grid-column: 29/38;
                }
            }
        }
    }
    #section2 {
        height: 860px;
        .section2-wrapper {
            height: 100%;
            padding: 20px 0 60px 0;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr 5fr;
            .title {
                flex: 1;
            }
        }
    }
    #section3 {
        height: 100%;
        .section3-wrapper {
            display: grid;
            margin: 0 auto 60px auto;
            grid-template-columns: 1fr 1fr;
            grid-template-rows: 1fr 2fr;
            place-items: center;
            .title {
                grid-row: 1/2;
                grid-column: 1/3;
            }
            img {
                margin: 0;
            }
            #achPic1 {
                grid-row: 2/3;
                grid-column: 1/2;
            }
            #achPic2 {
                grid-row: 2/3;
                grid-column: 2/3;
            }
        }
    }
    #section4 {
        height: 1600px;
        .section4-wrapper {
            height: 700px;
            display: grid;
            margin: 0 auto;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr 4fr 4fr;
            place-items: center;
            .title {
                grid-row: 1/2;
                grid-column: 1/2;
            }
            img {
                margin: 0;
                max-width: 100%;
            }
            #classPic1 {
                grid-row: 2/3;
                grid-column: 1/2;
            }
            #classPic2 {
                grid-row: 3/4;
                grid-column: 1/2;
            }
        }
    }
}

@media screen and (max-width: 767px) {
    #section1 {
        .section1-wrapper {
            padding: 0 0 40px 0;
            grid-template-rows: 1fr 10fr;
            .img-wrapper {
                grid-template-columns: 100%;
                grid-template-rows: repeat(5, 250px);
                gap: 30px;
                place-items: center;
                img {
                    width: 380px;
                    height: 250px;
                    max-width: 100%;
                }
                #camPic1 {
                    grid-row: 1/2;
                    grid-column: 1/2;
                }
                #camPic2 {
                    grid-row: 2/3;
                    grid-column: 1/2;
                }
                #camPic3 {
                    grid-row: 3/4;
                    grid-column: 1/2;
                }
                #camPic4 {
                    grid-row: 4/5;
                    grid-column: 1/2;
                }
                #camPic5 {
                    grid-row: 5/6;
                    grid-column: 1/2;
                }
            }
        }
    }
    #section2 {
        height: 100%;
        .section2-wrapper {
            padding: 0 0 40px 0;
            grid-template-rows: 1fr 10fr;
            .img-wrapper {
                grid-template-columns: 100%;
                grid-template-rows: repeat(5, 250px);
                gap: 30px;
                grid-template-areas:
                    'eve1'
                    'eve2'
                    'eve3'
                    'eve4'
                    'eve5';
                place-items: center;
                img {
                    width: 380px;
                    height: 250px;
                    max-width: 100%;
                }
                #eventsPic1 {
                    grid-area: eve1;
                }
                #eventsPic2 {
                    grid-area: eve2;
                }
                #eventsPic3 {
                    grid-area: eve3;
                }
                #eventsPic4 {
                    grid-area: eve4;
                }
                #eventsPic5 {
                    grid-area: eve5;
                }
            }
        }
    }

    #section3 {
        height: 100%;
        .section3-wrapper {
            padding: 40px 0 40px 0;
            grid-template-rows: 1fr 4fr 4fr;
            grid-template-columns: 1fr;
            grid-template-areas:
                'title'
                'pic1'
                'pic2';
            gap: 30px;
            .title {
                grid-area: title;
            }
            #achPic1 {
                grid-area: pic1;
            }
            #achPic2 {
                grid-area: pic2;
            }
        }
    }

    #section4 {
        height: 1400px;
    }
}
