.teachercard-container {
	position: relative;
	height: fit-content;
	padding-bottom: 800px;
	width: 100%;
	margin: 0 auto;	
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	.best-team-header {
		position: relative;		
		margin-bottom: 0px;
		width: 100%;
		margin-top: 11.2vh;
		font-family: Comfortaa;
		font-style: normal;
		font-weight: bold;
		font-size: 2.25rem;
		line-height: 2.25rem;
		text-align: center;
		color: #000000;
	}
	.best-team-content {
		position: relative;
		width: 80.3%;
		height: auto;		
		margin-top: 3.75rem;
		margin-bottom: 0;
		font-family: Avenir;
		font-style: normal;
		font-weight: 500;
		font-size: 1.5rem;
		line-height: 2.25rem;
		text-align: center;
		color: #333333;
		mix-blend-mode: normal;
	}
	.teachercard-wrapper {
		width: 100%;
		height: fit-content;		
		max-width: 1440px;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		overflow: hidden;	
		padding-bottom: 100px;
		.teachercard-item {
			width: 100%;
			max-width: 550px;
			position: relative;
			margin-top: 6.7vh;
			margin-left: 2.1%;
			margin-right: 2.1%;
			background: #FFFFFF;
			box-shadow: 0rem 30px 60px rgba(0, 0, 0, 0.15);
			border-radius: 4px;	
			display: flex;
			flex-direction: column;			
			flex-wrap: wrap;
			.teacher-fig {
				width: 550px;
				height: 550px;
				//object-fit: contains; /* Do not scale the image */
				object-position: center;
				background: #C4C4C4;		
				img {
					width: 100%;
					height: 100%;
					min-width: 100%;
					min-height: 100%;			
					border-radius: 4px;
				}
			}	
			.teacher-title {				
				margin-top: 2.5rem;				
				margin-left: 2.5rem;
				font-family: Avenir;
				font-style: normal;
				font-weight: 800;
				font-size: 1.125rem;
				line-height: 1.125rem;
				height: 1.125rem;
				align-items: center;		
				color: #000000;
			}
			.div-line {				
				margin-left: 2.625rem;			
				margin-top: 0.313rem;			
				background: #000000;
				width: 1.125rem;
				height: 0.125rem;
			}
			.teacher-name {				
				margin-left: 2.5rem;		
				margin-top: 1.25rem;							
				font-family: Avenir;
				font-style: normal;
				font-weight: 500;
				font-size: 1.5rem;
				line-height: 1.5rem;				
				color: #000000;
			}
			.teacher-description {
				margin-right: 2.5rem;	
				margin-top: 1.25rem;
				margin-bottom: 5.2%;				
				margin-left: 2.5rem;
				font-family: Avenir;
				font-style: normal;
				font-weight: normal;
				font-size: 1.125rem;
				line-height: 1.68rem;
				color: #555555;
				display: block;
			}
		}		
	}	
}
@media screen and (max-width: 767px) {
	.teachercard-container {
		width: 80%;
		padding-bottom: 55rem;
		.teachercard-wrapper {
			.teachercard-item {
				margin-bottom: 0.625rem;
				box-shadow: 0rem 5px 10px rgba(0, 0, 0, 0.15);
				.teacher-fig {
					position: relative;
					width: 100%;	
					padding-top: 100%;
					height: 0;			
					img {
						position: absolute;
						object-fit: cover;
						left: 0;
						right: 0;
						top: 0;
						bottom: 0;
					}					
				 }
			}
		}
	}
}
