
.slider-container {
	width: 100%;
	height: auto;
	overflow: hidden;	
	.slider-wrapper {
		width: 100%;		
		height: auto;	
		display: flex;		
		.slide-item {
			width: 100%;
			height: auto;	
			min-width: 100%;
			display: flex;
			justify-content: center;	
			.slide-inner-wrapper {
				position: relative;
				width: 80%;
				height: 0;
				padding-top: 61.86%;
				.image-layer {
					position: absolute;
					top: 0;
					width: 100%;
					height: 100%;
					min-height: 100%;									
					display: flex;
					justify-content: space-between;
					.quote-figure{
						width: 44.64%;				
						padding-top: 25.17%;
						padding-right: 36.85%;						
						padding-bottom: 45.5%;
						display: flex;
						justify-content: flex-start;
						img {							
							width: 100%;
							height: 100%;
						}
					}
					.banner-figure{
						flex: 1;
						padding-top: 8.13%;
						padding-bottom: 0;
						padding-right: 3.46%;
						img {
							width: 100%;
							height: 100%;
						}
					}
				}	
				.word-layer {
					position: absolute;
					top: 0;
					width: 100%;
					height: 100%;
					min-height: 100%;
					display: flex;
					flex-direction: column;
					.slogon {
						position: relative;
						width: 52%;
						height: 22%;
						padding-left: 5.02%;
						margin-top: 28.3737%;
						font-family: Avenir;
						font-style: normal;
						font-weight: 500;
						font-size: 2.5vw;		
						line-height: 1.33;
						letter-spacing: 0.615385px;
						color: #333333;
					}
					.name {
						position: relative;
						padding-left: 5.02%;
						margin-top: 17.3%;
						height: 2.076%;
						font-family: Avenir;
						font-style: normal;
						font-weight: 800;
						font-size: 1.66vw;
						line-height: 1;
						letter-spacing: 0.346154px;
						color: #000000;
					}
					.title {
						position: relative;
						padding-left: 5.02%;
						margin-top: 0.779%;
						font-family: Avenir;
						font-style: normal;
						font-weight: 500;
						font-size: 1.66vw;
						line-height: 1;
						color: #000000;
						mix-blend-mode: normal;
						opacity: 0.7;
					}
				}			
			}							
		}	
	}
	.dot-wrapper {		
		position: absolute;		
		display: flex;
		justify-content: flex-start;
		height: auto;
		width: 10vw;
		left: 0;
		top: 0;
		margin-left: 13.8%;
		margin-top: 57.29%;		
		.pic-dot {
			width: 1.04vw;
			height: 1.04vw;
			border-radius: 50% 50%;					
			margin-right: 1.04vw;		
			background: #CFE3FF;
			mix-blend-mode: normal;
			opacity: 0.2;
		}
	}	
}

